@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.nav {
    width : 100vw;
    height : 13vh;
    z-index: 99;
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    color: white;
    position: relative;
    transition: 0.75s;
    background-image: none;
    .logo {
      width : 13vh;
      height : 13vh;
      z-index: 100;
      background-image: url('https://cdn.discordapp.com/attachments/707152433461067846/720281775762440242/kompsiuter.png');
      background-size: cover;
      margin-left : 3vw;
    }
    .listItem {
        font-family: 'Montserrat',sans-serif;
        z-index: 99;
        background-image: none;
        margin-left : 3vw;

        .link {
            text-decoration: none;
            color : white;
        }
    }
}
.nav2 {
    @extend .nav;
    background-color: rgb(255, 174, 0);
    color : black;
    .listItem {
        .link {
            color : black;
        }
    }
}
body { 
    margin : 0;
    padding: 0;
}
