@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.everything{
    background-image: url('https://wallpaperaccess.com/full/2065685.jpg');
    background-size: cover;
    overflow-x: hidden;
    background-attachment: fixed;
    .whatIs {
        padding-top: 35vh;
        h1,h2 {
            text-align: center;
            font-family: 'Montserrat',sans-serif;
            color : whitesmoke;
            position: relative;
        }
        h1 {
            animation: appear 2s;
        }
        h2 {
            max-width: 75vw;
            margin-left : 12.5vw;
            animation: appear 4s;
        }
    }
.sectionItem{
    padding-top: 50vh;
    font-family: 'Montserrat',sans-serif;
    color: white;
    display: flex;
    justify-content: center;
    .text {
        margin-right: 5vw;
        h1 {
            text-align: center;
        }
        .p {
            display: block;
            word-wrap: break-word;
            max-width: 30vw;
            font-size: medium;
        }
    }
    .img{
        display: block;
        margin-left: 5vw;
        padding-top: 10vh;
        img {
            width : 30vw;
            height : 20vw;
        }
    }
    
}
}
@keyframes appear {
    0% {
        opacity: 0;
        top : 65px;
    }
    100% {
        opacity: 1;
        top : 0;
    }
}

  