@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.all {
    font-family: 'Montserrat',sans-serif;
    padding-top: 45vh;
    position: relative;
    background-image: none;
    h1,h2 {
        text-align: center;
        color : whitesmoke;
        transform: scale(1.2);
        background-image: none;
        position: relative;
    }
    h1 {
        animation: appear 2s;
    }
    h2 {
        animation: appear 4s;
    }
}
body {
    background-image: url('https://wallpapercave.com/wp/wp2864012.gif');
    background-size: cover;
    overflow-x: hidden;
}

@keyframes appear {
    0% {
        opacity: 0;
        top : 65px;
    }
    100% {
        opacity: 1;
        top : 0;
    }
}

  