@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.everything{
    background-image: url('https://wallpaperaccess.com/full/2065685.jpg');
    background-size: cover;
    overflow-x: hidden;
    background-attachment: fixed;
.sectionItem{
    padding-top: 25vh;
    font-family: 'Montserrat',sans-serif;
    color: white;
    display: flex;
    justify-content: center;
    .text {
        margin-right: 5vw;
        h1 {
            text-align: center;
        }
        .p {
            display: block;
            word-wrap: break-word;
            max-width: 30vw;
            font-size: medium;
        }
    }
    .img{
        display: block;
        margin-left: 5vw;
        padding-top: 10vh;
        img {
            width : 30vw;
            height : 20vw;
        }
    }
    
}
}